.header{
    position: fixed;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 70px;
    display: flex;
    left: 0;
    top: 0;
    padding: 0 60px;
    box-sizing: border-box;
    z-index: 100;
}
.header .hgnb_wrap{
    display:flex;
    align-items: center;
    gap:45px;
}
.header .hgnb_wrap .gnb{
    display:flex;
    gap: 22px;
}
.header .logo{
    width: 90px;
    height: 26px;
}
.header .avatar{
    width: 32px;
}
.icon_wrap{
    display:flex;
    gap: 22px;
    align-items: center;
    cursor: pointer;
}
.icon_wrap img{
    border-radius: 4px;
}
li{
    cursor: pointer;
}
li:hover{
    color: red;
}
.header-black{
    background: #141414;
}
.down{
    margin-left: -18px;
}