.modal_wrap{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.8);
    z-index: 1000;
}
.modal{
    position: absolute;
    max-width: 920px;
    /* height: auto; */
}
.modal .btn_close{
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
    color: #fff;
    width: 35px;
    height: 35px;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 100%;
    text-align: center;
    line-height: 35px;
}
.modal_img{
    width: 100%;
    height: auto;
    border-radius: 10px 10px 0 0;
}
.modal_box{
    position: relative;
    border-radius: 10px 10px 0 0;
}
.modal_box h3{
    position: absolute;
    left: 50px;
    bottom: 130px;
    font-size: 38px;
}
.modal_box button{
    padding: 4px 22px;
    position: absolute;
    left: 50px;
    bottom: 80px;
    border: none;
    border-radius: 4px;
    font-size: 18px;
    background: #fff;
    cursor: pointer;
}
.modal_box button:hover{
    background: #777;
    color: #fff;
}
.modal_box .icon_wrap {
    position: absolute;
    left: 160px;
    bottom: 84px;
    font-size: 24px;
    display: flex;
    cursor: pointer;
}
.content{
    padding: 20px 50px ;
    background: rgba(87, 84, 84, 0.4);
    border-radius: 0 0 20px 20px ;
}
.content .con1{
    font-weight: bold;
    color: #24951f;
}
.content h2{
    text-indent: -60px;
}
.content .over{
    margin-top: 10px;
}

