.banner{
    width: 100%;
    height: 100vh;
    object-fit: contain;
    box-sizing: border-box;
    /* border:5px solid red; */
    position: relative;
    background-repeat: no-repeat !important;
    background-position: center top !important;
    background-size: cover !important;
}
.banner .contents{
    position: absolute;
    left: 70px;
    bottom: 140px;
}
.banner .contents h1{
    font-size:80px;
    font-weight: bold;
    padding-bottom: 50px;
}
.banner .contents .description{
    width: 720px;
    line-height: 1.5;
    font-weight: bold;
    font-size: 20px;
}
.banner .contents .btn_wrap{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 15px; 
    padding-bottom: 50px;  
}
.banner .contents .btn_wrap button{
    border: none;
    background: none;
    padding:15px 35px;
    color:#fff;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
    border-radius: 8px;
}
.banner .contents .btn_wrap .play{
    background: #fff;
    color:#141414;  
}
.banner .contents .btn_wrap .play:hover{
    background: rgba(114,106,104,0.5);
    color:#fff
}
.banner .contents .btn_wrap .info{
    background: #726a68;
    color:#fff;   
}
.banner .contents .btn_wrap .info:hover{
    background: #fff;
    color:#141414;  
}

