.allmovie{
    width: 100%;
}
.posters{
    display:flex;
    padding-left:60px;
}
.allmovie_img{
   width: 100%;
   object-fit: contain;
   max-width:200px;
   margin-right:10px;
   transition: all 0.5s ease-in-out;
   cursor: pointer;
}
.allmovie_img:hover{
    transform: scale(1.1);
}
.allmovie h2{
   margin: 60px 0 20px 0;
   padding-left: 60px; 
}