a{
    text-decoration: none;
}
.list_wrap{
    display: flex;
    gap:165px;
}
.list_wrap ul li a{
    color: #777;
    font-size: 12px;
    line-height: 38px;
    cursor: pointer;
}
.list_wrap ul li a:hover{
    color: #ccc;
}
.icon span{
    margin-right: 20px;
    font-size: 26px;
}
.footercontent button{
    width: 80px;
    height: 30px;
    background: none;
    border: 1px solid #777;
    color: #777;
    margin: 30px 0;
    cursor: pointer;
}
.footercontent button:hover{
    background: #ccc;
}
.footercontent .icon{
    cursor: pointer;
}
.footer_wrap p{
    color: #777;
    font-size: 12px;
    line-height: 20px;
}